* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  border: none;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(90deg, rgba(4, 30, 138, 1) 24%, rgb(2, 147, 192) 63%, rgba(0, 136, 244, 1) 100%);
  width: 100%;
  height: 16vh;
  padding: 20px 10%;
}

.logo {
  color: #fff;
}

span {
  color: #00c8ff;
  margin-right: 20px;
}

.navlinks ul {
  display: flex;
}

.navlinks ul li {
  margin-right: 40px;
  font-size: 1.6rem;
  border-right: 3px solid black;
  padding: 0 30px;
  color: #fff;
  cursor: pointer;
}

.navlinks ul li:hover {
  border: 1px solid #fff;
  border-radius: 10px;
  text-decoration: none;
}
